import React from 'react';
import axios from 'axios';
import AllInfo from './AllInfo';
import {Link} from 'react-router-dom';






class Clients extends React.Component{


    constructor(props){
        super(props);
        this.deleteCurrentClient = this.deleteCurrentClient.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.applyFilter = this.applyFilter.bind(this);
        this.clearFilter = this.clearFilter.bind(this);
        this.state={
            ClientsInfo:[],
            ClientsInfoOriginal:[],
            IndividualClient:{},
            userType:'',
            startdate:'',
            filterApplied:false,

        }

    }

    handleChange= input =>e=>{
        this.setState({[input]:e.target.value});
       
    }

    componentDidMount(){

        const url=`https://olemissoutreach.herokuapp.com/admin/viewdata`;

        axios.get(url)
        .then((Response)=>{

            this.setState({
                ClientsInfo:Response.data,
                ClientsInfoOriginal:Response.data,
            })

        })
        .catch((error)=>{
            console.log(error);
        });
        
        

    }

    clearFilter(){


        this.setState({filterApplied:!this.state.filterApplied})


        this.setState({

            ClientsInfo:this.state.ClientsInfoOriginal,
            userType:'',
            startdate:'',
        

        })

    }

    applyFilter(){

        console.log("button clicked")

        this.setState({filterApplied:true});



        if(this.state.userType !== ''){

            this.setState({
                ClientsInfo:this.state.ClientsInfo.filter(clientItem => clientItem.recruiterProgram === this.state.userType)
            })

        }

        else if(this.state.startdate !== ''){

            this.setState({
                ClientsInfo:this.state.ClientsInfo.filter(clientItem => JSON.stringify(clientItem.eventDate).substring(1,11) === this.state.startdate)
            })

        }

        else if(this.state.userType !== '' && this.state.startdate !== ''){


            this.setState({
                ClientsInfo:this.state.ClientsInfo.filter(clientItem => clientItem.recruiterProgram === this.state.userType && JSON.stringify(clientItem.eventDate).substring(1,11) === this.state.startdate),
         
          
            })

            

        }

        console.log(this.state.startdate)
        


       

    }

    deleteCurrentClient(item){
       

        console.log("button Clicked");
        console.log(item);



      
        const url = `https://olemissoutreach.herokuapp.com/admin/${item}`;

        axios.delete(url).then((Response) =>{
            console.log(Response)
         

        }).catch((error)=>{
            console.log(error);
        });
      
        this.setState
        ({ClientsInfo:this.state.ClientsInfo.filter(clientItem => clientItem._id !== item),
          ClientsInfoOriginal:this.state.ClientsInfoOriginal.filter(clientItem => clientItem._id !== item)})
        
        console.log("button Clicked");


    }

    











    render(){
        return(



            <>

            <div className="container">

             <div><Link style={{ textDecoration: 'none', color:'white' }} to='/admin' className ="Downloadbutton">Launch Dashboard</Link></div>
             

                <AllInfo client={this.state.ClientsInfo} deleteCurrentClient={this.deleteCurrentClient} handleChange={this.handleChange} applyFilter={this.applyFilter} clearFilter={this.clearFilter} filterApplied={this.state.filterApplied} />


            </div>
            </>
        )
    }
}

export default Clients;

