import React from 'react';
import Delete  from '../images/delete.png';
import CsvDownload from 'react-json-to-csv';
import FormHeader from './FormHeader';



const AllInfo = (props) =>{






    return(

       
            <>
            <div style={{margin:'120px 0px'}}>
            <div class="adminCard">

                        <div class="row card-body">
                        
                        <div className="col-lg-3 col-md-3 col-sm-12 ">
                        <select  style={{width:'100%',height:'45px',backgroundColor:'rgb(45,128,210)',color:'white',padding:'0', margin:'0'}}
                        
                        name="userType"
                        onChange={props.handleChange('userType')}
                        >
                        <option  value="">Select The Program </option>
                        <option value="College Program">College Program</option>
                        <option value="Outreach">Division of Outreach</option>
                        <option value="General Studies">General Studies</option>
                        <option value="Pre College">Pre College</option>
                        <option value="Admin">Admin</option> 
                        
                        
                        </select>
                        </div>

                        <div className="col-lg-3 col-md-3 col-sm-12">
                    
                       
                        <input

                        type="date"
                        name = "startdate"
                        onChange={props.handleChange('startdate')}

                        />

                  
                        </div>
                    
                        {props.filterApplied === false &&  <div className="col-lg-3 col-md-3 col-sm-12"><button onClick={props.applyFilter} style={{width:'100%'}}className="Downloadbutton">Apply Filter</button></div>}
                        {props.filterApplied && <div className="col-lg-3 col-md-3 col-sm-12"><button onClick={props.clearFilter} style={{width:'100%'}}className="Downloadbutton">Clear Filter</button></div>}
                        <div className="col-lg-3 col-md-3 col-sm-12"><CsvDownload  data={props.client} filename="FormData.csv" style={{width:'100%'}} className ="Downloadbutton">Download Data</CsvDownload></div>
                        
                       

                        </div>
                       
                               
                        
        </div>

        <br/>

        <FormHeader />
        
          
        {props.client.map((item)=>(

            

                
                <>

                <hr class="my-4"></hr>

                <div className="row recruiterDetails">
                

                <div className="col-lg-3 col-md-12 col-sm-12 text-center recruiterName detailText">{item.recruiterFirstName} {item.recruiterLastName}</div>
                <div className="col-lg-2 col-md-12 col-sm-12 text-center detailText">{item.recruiterProgram}</div>
                <div className="col-lg-3 col-md-12 col-sm-12 text-center detailText">{item.eventName}</div>
                <div className="col-lg-2 col-md-12 col-sm-12 text-center detailText">{item.eventDate? (JSON.stringify(item.eventDate).substring(1,11)): ''}</div>
                <div className="col-lg-2 col-md-12 col-sm-12 text-center "><button onClick={props.deleteCurrentClient.bind(this, item._id)} className="deleteButton1"><img class className="deleteIcon" src={Delete} alt ="delete icon"/></button></div>

                </div>

                </>
            ))}


            <br/>
           

            </div>
            </>

     
          
    );
}
export default AllInfo;
