import React,{Component} from 'react';
import fire from '../auth/firebase';
import RecruiterDetailPrinter from './RecruiterDetailPrinter';





class  ManageRecruiter extends Component{


    constructor(props){

    super(props);
    this.state={
        recruiterDetails:[],
  
    }



    }


    componentDidMount(){




     var recruiterDetails1 = [];
   
     this.state.recruiterDetails =[];
    

    

    if(fire.database().ref().child("Recruiter")){

   
    fire.database().ref("Users").orderByChild("Recruiters").on("value",snap=>{

        if(snap.exists()){
           
           
            const data = snap.val();
         
            const newData= data[Object.keys(data)[0]];

          
            const finalNewData = newData[Object.keys(newData)[0]];

        
         
          
           
            
            Object.keys(finalNewData).forEach(function (key){

        


               


                recruiterDetails1.push(<RecruiterDetailPrinter firstName={finalNewData[key].firstName}
                                                      lastName={finalNewData[key].lastName} 
                                                      email={finalNewData[key].email} 
                                                      userType={finalNewData[key].userType}
                                                      userID={key}/>
                                                      );
            });

            this.setState({recruiterDetails:recruiterDetails1})

            recruiterDetails1 =[];
        }

        else{
            recruiterDetails1=[];
            this.setState({recruiterDetails:recruiterDetails1})

        }

        

    });

}




    }


    render(){

        return(

            <>

                {this.state.recruiterDetails}

         
             </>
         
             )

    }
    

}


export default ManageRecruiter;