import React,{Component} from 'react';
import UMError from '../images/UMError.png';
import {Link} from 'react-router-dom';
import Error from '../css/error.css';






class PageNotFound extends Component{

    constructor(props){
        super(props);
        this.state={
            path:'',
            pathName:'',
        }
    }


    componentDidMount(){

        let data = sessionStorage.getItem("mySessionStorageData");
        data = JSON.parse(data);

        if(data){

            this.setState({path:`/${data.userType}`,pathName:`${data.userType} Dashboard`});

        }
        else{

            this.setState({path:`/`,pathName:'Login Page'});

        }
    }
    render(){
        return(

            <>
            <div className="container">

                <div className="notfounderrorText">
                    
                    <p>Sorry, we couldn't find the requested page</p>
                    <p>Please try login again, go to <Link to={this.state.path}>{this.state.pathName}</Link></p>
             
                    
                    
                    
                </div>

                <img src={UMError} alt="University Error Graphics"/>
                
            </div>

            
            </>
        


        )
           
    }
}
export default PageNotFound;